@import "color";
.about{
width: 100%;
background-color: $pink;
>main{
    background-color: white;
    max-width: 900px;
    height: 100%;
    margin: auto;

    >h1{
        font: 500 3rem "Roboto";
        color: #fff;
        text-transform: uppercase;
        background-color: $dark;
        padding: 1rem 2rem;
        margin-top: 5rem;
        //text-align: center;
    }
    > article {
        margin: 2rem 0;
    


        >h4{
            font: 100 2rem "Roboto";
            text-transform: uppercase;
            text-align: center;
            padding: 2rem 0rem;
        }

        >p{
            letter-spacing: 1px;
            word-spacing: 3px;
       
          width: 50%;
            text-align: center;
            margin: auto;
            padding: 2rem ;
        
        }
        > a {
            background-color: $dark;
            border-radius: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 2.5rem;
            height: 2.5rem;
            color: white;
            font-size: 1.5rem;
            transition: all 0.3s;
            margin: auto;
  
            &:hover {
              color: $red;
              transform: scale(1.1);
            }
          }

    }

    
    > div {
        background-color: $red;
        padding: 2rem;
        color: white;
        > h2 {
          text-align: center;
          text-transform: capitalize;
        }

        
      > article {
        display: flex;
        align-items: center;
        justify-content: center;
        > div {
            margin: 2rem;
            > img {
              width: 150px;
              height: 150px;
              border-radius: 100%;
            }
            >h3{
                margin: 1rem;
            }

        }
        >p{
            margin: 0 1rem;
            width: 50%;
            letter-spacing: 2px;
            line-height: 150%;
        }
      }

    }
 
    
}

}

@media screen and (max-width: 600px) {
    .about > main {
      > h1 {
        font: 100 3rem "Roboto";
        text-align: center;
      }
  
      > article > p {
        width: 100%;
      }
  
      > div > article {
        flex-direction: column;
  h2{
    text-align: center;
  }
        > p {
          text-align: center;
        }
      }
    }
  }
  